import React from 'react'
import {graphql, Link, useStaticQuery} from 'gatsby'

function FindPropertiesHomeEight() {
     const data = useStaticQuery(graphql`
	query{
		pc1: file(relativePath: {eq: "property/pc1.png"}) {
			id
			childImageSharp {
			  fluid {
				originalImg
			  }
			}
		}
		pc2: file(relativePath: {eq: "property/pc2.png"}) {
			id
			childImageSharp {
			  fluid {
				originalImg
			  }
			}
		}
		pc3: file(relativePath: {eq: "property/pc3.png"}) {
			id
			childImageSharp {
			  fluid {
				originalImg
			  }
			}
		}
		pc4: file(relativePath: {eq: "property/pc4.png"}) {
			id
			childImageSharp {
			  fluid {
				originalImg
			  }
			}
        }
        pc5: file(relativePath: {eq: "property/pc5.png"}) {
			id
			childImageSharp {
			  fluid {
				originalImg
			  }
			}
        }
        mouse: file(relativePath: {eq: "resource/mouse.png"}) {
			id
			childImageSharp {
			  fluid {
				originalImg
			  }
			}
        }
	}`
)
    return (
        <section id="property-city" className="property-city pb30 bb1">
		<div className="container">
			<div className="row">
				<div className="col-lg-6 offset-lg-3">
					<div className="main-title text-center">
						<h2>Find Properties in These Cities</h2>
						<p>Lorem ipsum dolor sit amet, consectetur adipiscing elit.</p>
					</div>
				</div>
			</div>
			<div className="row">
				<div className="col-sm-6 col-lg-4 col-xl">
				<Link to="/half-map/v1">
					<div className="properti_city_home8 text-center">
						<div className="thumb">
							<img className="img-fluid w100" src={data.pc1.childImageSharp.fluid.originalImg} alt="pc1.png"/>
						</div>
						<div className="details">
							<h4>Miami</h4>
							<p>24 Properties</p>
						</div>
					</div>
				</Link>
				</div>
				<div className="col-sm-6 col-lg-4 col-xl">
				<Link to="/half-map/v1">
					<div className="properti_city_home8 text-center">
						<div className="thumb">
							<img className="img-fluid w100" src={data.pc2.childImageSharp.fluid.originalImg} alt="pc2.png"/>
						</div>
						<div className="details">
							<h4>New York</h4>
							<p>24 Properties</p>
						</div>
					</div>
				</Link>
				</div>
				<div className="col-sm-6 col-lg-4 col-xl">
				<Link to="/half-map/v1">
					<div className="properti_city_home8 text-center">
						<div className="thumb">
							<img className="img-fluid w100" src={data.pc3.childImageSharp.fluid.originalImg} alt="pc3.png"/>
						</div>
						<div className="details">
							<h4>Rome</h4>
							<p>24 Properties</p>
						</div>
					</div>
				</Link>
				</div>
				<div className="col-sm-6 col-lg-4 col-xl">
				<Link to="/half-map/v1">
					<div className="properti_city_home8 text-center">
						<div className="thumb">
							<img className="img-fluid w100" src={data.pc4.childImageSharp.fluid.originalImg} alt="pc4.png"/>
						</div>
						<div className="details">
							<h4>Paris</h4>
							<p>24 Properties</p>
						</div>
					</div>
				</Link>
				</div>
				<div className="col-sm-6 col-lg-4 col-xl">
				<Link to="/half-map/v1">
					<div className="properti_city_home8 text-center">
						<div className="thumb">
							<img className="img-fluid w100" src={data.pc5.childImageSharp.fluid.originalImg} alt="pc5.png"/>
						</div>
						<div className="details">
							<h4>Sydney</h4>
							<p>24 Properties</p>
						</div>
					</div>
				</Link>
				</div>
			</div>
		</div>
	</section>
    )
}

export default FindPropertiesHomeEight
